/* Todo List */

:root {
  --red-50: #ffebee;
  --red-600: #e53935;
  --green-800: #2e7d32;
  --yellow-50: #fffde7;
  --yellow-600: #fdd835;
  --orange-50: #fff3e0;
  --orange-600: #fb8c00;
  --gray-100: #f5f5f5;
  --gray-400: #bdbdbd;
  --gray-900: #212121;
  --white: #fff;
}

body {
  font-family: Inclusive Sans, Arial, Helvetica, sans-serif;
  color: var(--gray-900);
  position: relative;
}

a,
button {
  cursor: pointer;
}

a.primary,
button.primary {
  font-weight: bold;
}

.hidden {
  display: none;
}

.content {
  display: grid;
  grid-template-columns: 240px 1fr;
  grid-template-rows: 128px 1fr; /* 80px padding + 48px font */
  grid-template-areas:
    'nav header'
    'nav main';
  gap: 40px;
  height: 100vh;
}

nav {
  grid-area: nav;
  padding: 40px;
  background-color: var(--gray-100);
}

nav ul {
  list-style: none;
  padding-left: 0;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

nav ul a {
  display: block;
  padding: 5px 10px;
  border-radius: 5px;
}

nav ul a:hover {
  background-color: var(--white);
}

nav ul a.primary {
  color: var(--green-800);
}

nav ul a.primary,
nav ul a.secondary {
  position: relative;
  padding-left: 30px;
}

nav ul a.primary:before,
nav ul a.secondary:before {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 10px;
  content: '';
  display: block;
  width: 15px;
  height: 15px;
  margin-right: 5px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 15px;
}

nav ul a.primary:before {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
}

nav ul a.secondary:before {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_5___});
}

header {
  grid-area: header;
  padding-top: 80px;
}

main {
  grid-area: main;
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding-right: 40px;
  padding-bottom: 80px;
}

article {
  position: relative;
  border-top: 2px solid var(--gray-100);
  padding-top: 20px;
  padding-left: 60px;
  min-height: 80px;
}

article .is-done-btn {
  background: none;
  position: absolute;
  top: 25px;
  left: 30px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 2px solid var(--gray-400);
}

article .is-done-btn[data-is-done='true'] {
  background-position: center;
  background-repeat: no-repeat;
  background-size: 15px;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_6___});
  border: 1px solid var(--gray-900);
}

article .priority-btn {
  position: absolute;
  top: 20px;
  left: 0;
  width: 20px;
  padding-left: 2px;
  padding-right: 2px;
  border-radius: 5px;
  border: 2px solid var(--gray-400);
  text-align: center;
  background: none;
}

article .priority-btn[data-priority='1'] {
  border-color: var(--red-600);
  background-color: var(--red-50);
}

article .priority-btn[data-priority='2'] {
  border-color: var(--orange-600);
  background-color: var(--orange-50);
}

article .priority-btn[data-priority='3'] {
  border-color: var(--yellow-600);
  background-color: var(--yellow-50);
}

article h2 {
  display: flex;
  align-items: center;
  position: relative;
  margin-bottom: 5px;
  line-height: 1.2;
  cursor: pointer;
}

article h2:after {
  content: '';
  display: block;
  width: 15px;
  height: 15px;
  margin-left: 5px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 15px;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_7___});
}

article h2.open:after {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_8___});
}

article .due-date,
article .edit-btn,
article .delete-btn {
  position: relative;
  padding-left: 20px;
}

article .delete-btn {
  margin-left: 15px;
}

article .due-date:before,
article .edit-btn:before,
article .delete-btn:before {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  content: '';
  display: block;
  width: 15px;
  height: 15px;
  margin-right: 5px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 15px;
}

article .due-date:before {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_9___});
}

article .edit-btn:before {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_10___});
}

article .delete-btn::before {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_11___});
}

article .edit-btn:hover,
article .delete-btn:hover {
  text-decoration: underline;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--gray-900);
  opacity: 0.5;
}

dialog {
  position: absolute;
  top: 200px;
  left: 50%;
  transform: translateX(-50%);
  border: 5px solid var(--gray-900);
  border-radius: 10px;
  padding: 20px;
  width: 400px;
}

form h2 {
  margin-bottom: 20px;
}

.form-row {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

form label {
  font-weight: bold;
  margin-bottom: 5px;
}

form input {
  border-radius: 10px;
  padding: 5px 10px;
  border: 2px solid var(--gray-400);
}

form input:user-invalid {
  border-color: var(--red-600);
}

form .button-row {
  display: flex;
  justify-content: end;
  gap: 5px;
}

form button {
  background: none;
  border: 2px solid var(--gray-400);
  border-radius: 10px;
  padding: 5px 10px;
}
